import Swiper from 'swiper';

$('.main-page-slider').each((index, component) => {
	let $component = $(component);
	let $sliderContainer = $component.find('.js-slider');
	let $arrowPrev = $component.find('.js-arrow-prev');
	let $arrowNext = $component.find('.js-arrow-next');
	let $activeIndex = $component.find('.js-counter-active');
	let $slideLength = $component.find('.js-counter-length');

	let swiper = new Swiper($sliderContainer[0], {
		slidesPerView: 1,
		navigation: {
			prevEl: $arrowPrev,
			nextEl: $arrowNext,
		}
	});

	$slideLength.html(swiper.slides.length);

	swiper.on('slideChange', () => {
		$activeIndex.html(swiper.activeIndex + 1);
	});
});



