import 'fullpage.js/vendors/scrolloverflow';
import Fullpage from 'fullpage.js';

$('.js-fullpage').each((index, component) => {
	let $component = $(component);
	let $fullpageContainer = $component.find('.js-fullpage-container');
	let $verticalNav = $component.find('.js-vertical-nav');

	function generateNavContent(counter) {
		for (let i = 0; i < counter; i++) {
			let target = $fullpageContainer.find('.section').eq(i).data('anchor');
			let $item = `<a href="#${target}" class="vertical-nav__item"></a>`;
			$verticalNav.find('.vertical-nav__content').append($item);
		}

		$verticalNav
			.find('.vertical-nav__item').eq(0)
			.addClass('vertical-nav__item_active');
	}

	if ($(window).innerWidth() > 1024) {
		generateNavContent($fullpageContainer.find('.section').length);

		let fullpageComponent = new Fullpage($fullpageContainer[0], {
			licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
			scrollOverflow: true,
			continuousVertical: false,
			scrollingSpeed: 700,
			onLeave: (origin, destination, direction) => {
				if ($verticalNav) {
					$verticalNav
						.find('.vertical-nav__item')
						.removeClass('vertical-nav__item_active');
					$verticalNav
						.find('.vertical-nav__item').eq(destination.index)
						.addClass('vertical-nav__item_active');
				}

				let destinationColor = destination.item.getAttribute('data-color');

				switch (destinationColor) {
					case 'hidden':
						$(window).trigger('main-page-logo:show:3');
						return;
					case 'negative':
						$(window).trigger('main-page-logo:show:2');
						return;
					case 'basic':
						$(window).trigger('main-page-logo:show:1');
						return;
					default:
						return;
				}
			}
		});

		fullpage_api.setAllowScrolling(true);
	}
});



